.home-title {
	@include fs(20rem);
	text-transform: uppercase;
	color: $main;
	font-weight: 700;
	margin-bottom: 30px;
	@include media-breakpoint-down(lg) {
		@include fs(18rem); }
	@include media-breakpoint-down(md) {
		@include fs(18rem); }
	&.icon {
		padding-left: 30px;
		@include pos(rel,null);
		@include fs(20rem);
		font-weight: 600;
		color: $blue;
		@include media-breakpoint-down(sm) {
			@include fs(16rem); }
		&:before {
			content: '';
			display: block;
			width: 31px;
			height: 30px;
			background: url(../img/home-icon.png) center center no-repeat;
			background-size: contain;
			@include pos(abs, -4px null null 0);
			@include media-breakpoint-down(sm) {
				top: -7px; } } } }
.home-des {
	@include fs(15rem);
	font-weight: 500;
	color: $main;
	max-width: 80%;
	margin: 0 auto 40px;
	@include media-breakpoint-down(sm) {
		max-width: 100%; } }
