	@include media-breakpoint-down(md) {
		max-width: 100%; }
	@include media-breakpoint-down(sm) {
		@include fs(13rem); } }

.home-farm {
	padding: 50px 0;
	background: url(../img/farm/homebg.png) center bottom no-repeat;
	background-size: 100% auto;
	@include pos(rel,null);
	@include media-breakpoint-down(sm) {
		background-size: 200% auto; }
	&:after {
		content: '';
		display: block;
		width: 50px;
		height: 48px;
		background: url(../img/titleicon.png) center center no-repeat;
		@include pos(abs, null null -26px 50%);
		@include transform(translateX(-50%)); }

	.workflow-list {
		.workflow {
			text-align: center;
			margin-bottom: 40px;
			border-top: 4px solid $green;
			&.heo {
				border-top-color: #007c85;
				figure {
					.wf-name {
						color: #007c85; } } }
			&.tom {
				border-top-color: #3472bf;
				figure {
					.wf-name {
						color: #3472bf; } } }
			figure {
				.wf-name {
					@include fs(18rem);
					padding: 20px 0;
					color: $green;
					font-weight: 800;
					text-transform: uppercase;
					@include media-breakpoint-down(md) {
						@include fs(15rem); } }
				.wf-img {
					img {
						width: 100%; } }
				figcaption {
					padding: 15px;
					@include media-breakpoint-down(md) {
						padding: 15px 0; } }
				.wf-des {
					@include fs(14rem); } } } }
	.workflow-intro {
		border-top: 4px solid #93b83c;
		.wf-name {
			@include fs(18rem);
			padding: 20px 0;
			color: #93b83c;
			font-weight: 800;
			text-transform: uppercase;
			text-align: center;
			@include media-breakpoint-down(md) {
				@include fs(15rem); } }
		.intro-content {
			background: url(../img/farm/home/patternbg.jpg) center center repeat;
			box-shadow: 3px 5px 25px color-rgba($black,0.05);
			padding: 15px;
			margin-bottom: 150px;
			@include media-breakpoint-down(md) {
				margin-bottom: 100px; }
			.row {
				margin: 0px -7px;
				.col-12 {
					padding: 0px 7px; } }
			.img {
				margin-bottom: 20px;
				img {
					width: 100%; } }
			.content {
				max-width: 90%;
				font-weight: 500;
				margin: 0 auto 10px;
				@include media-breakpoint-down(sm) {
					max-width: 100%; } } } } }
